import { DATES_KEY } from '../../components/Topics/constants/dates';

import { setUpDatesQuiz } from '../dates/utils';

function getRandomisedQuiz(quiz, quizLength) {
  switch (quiz) {
    case DATES_KEY:
        return setUpDatesQuiz(quizLength);
    default:
      break;
  }
}

export { getRandomisedQuiz };